* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  header {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  form {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
  
  
  .todo-container {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .list-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  
  li {
    list-style: none;
  }